.SidebarMan {
    display: flex;
    flex-direction: column;
    position: relative;
    padding-top: 4rem;
    transition: all 300ms ease;
  }
  /* logo */
  .bars{ 
    display: none;
  }
  
  .logoMan {
    display: flex;
    height: 5rem;
    font-weight: bold;
    font-size: 22px;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    height: 4%;
    padding-top: 37px;
  }
  .logoMan > span > span {
    color: var(--pink);
  }
  
  .logoMan > img {
    width: 3rem;
    height: 3rem;
  }
  
  
  /* menu */
  .menuMan {
    margin-top: 6rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  .menuItemMan {
    display: flex;
    align-items: center;
    gap: 2rem;
    height: 2.5rem;
    margin-left: 2rem;
    position: relative;
    transition: all 300ms ease;
    border-radius: 0.7rem;
    font-size: 14px;
  }
  
  .menuItemMan:hover {
    cursor: pointer;
  }
  
  .menuMan .menuItemMan:last-child {
    position: absolute;
    bottom: 2.3rem;
    width: 100%;
  }
  
  .activeMan {
    background: var(--activeItem);
    margin-left: 0;
  }
  .activeMan::before {
    content: "";
    width: 8px;
    height: 100%;
    background: var(--pink);
    margin-right: calc(1rem - 8px);
  }
  
  
  /* Tablets */
  @media screen and (max-width: 1200px) {
    .menuItemMan>span{
      display: none;
    }
    .logoMan{
      display: none;
    }
  }
  
  @media screen and (max-width: 768px) {
    .sidebarMan{
      position: fixed;
      z-index: 9;
      background: #facbcb;
      width: 55%;
      padding-right: 1rem;
      height: 100%;
    }
    .menuItemMan>span{
      display: block;
    }
    .logoMan{
      display: flex;
    }
    .menuMan .menuItemMan:last-child {
      position: relative;
      margin-top: 6rem;
    }
    .bars{
      display: flex;
      position: fixed;
      top: 2rem;
      left:60%;
      background:#0c0808;
      padding:10px;
      border-radius: 10px;
      z-index: 9;
    }
    .close{
      left: -60%;
    }
  }
  